body {

  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  background-image: url("https://images.pexels.com/photos/1105386/pexels-photo-1105386.jpeg?auto=compress&cs=tinysrgb&h=350");
  background-size: cover;
  /* background-color: #16121254;   */
  color: white;
  border-radius: 0.4em;
}

/* no repeat fixed top center */

code {
  font-family: 'Roboto', sans-serif;
}

.auth-container{ 
    padding: 2em;
    border: 1 px solid #000000a1;
    border-radius: 0.4em;
    box-shadow: 2px 4px 20px #0000008f;
    background-color: rgba(0, 0, 0, 0.5);
    color: rgb(182, 182, 182);
    width: 25em;
    margin-left: auto;
    margin-right: auto;
    font-family: 'Roboto', sans-serif;
    

    
}
.button {
    padding: .4em;
    text-align: center;
    height: 2.5em;
}

.login-col {
    border-left: 1px solid black;
}

.container {
    font-family: 'Roboto', sans-serif;
    color: white;
    background-color: rgba(0, 0, 0, 0.5);
}


.App {
  padding: 0%;
  margin:0%;
  height: 970px;
  background-size: 100% 100%;
